.social__list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin-left: -37px;
  margin-top: 33px;
}

.social__item {
  margin-right: 6px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 48px;
  background-color: @lightblue;

  &:hover {
    background-color: @blue;
  }

  &:active {
    background-color: @darkblue;
  }
}
