@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1280px) {
  .container {
    display: grid;
    width: 1200px;
    margin: 0 auto;
    padding: 0 0;
    box-shadow: 0 0 15px rgba(0, 1, 1, 0.2);
  }
}
.page-body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}
.page-header {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin-bottom: 40px;
}
.page-header__logo {
  position: absolute;
  order: 1;
  left: 50%;
