  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #81b3d2;
}
.main__toggle {
  position: absolute;
  left: 0;
  right: 0;
  background-color: #ffffff;
  height: 56px;
}
.main-nav__toggle {
  position: absolute;
  top: 16px;
  right: 20px;
  bottom: 0;
  display: block;
  width: 23px;
  height: 22px;
  cursor: pointer;
  border: none;
  border-radius: none;
  background: url("../img/icon-menu.svg.svg");
}
@media (min-width: 768px) {
