/* Cтили для чекбоксов */
.feedback__list--checkbox label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  padding-left: 30px;
}
.feedback__list--checkbox input + label::before {
  content: "";
  position: absolute;
  left: -50px;
  bottom: -11px;
  width: 54px;
  height: 55px;
  background-color: #f2f2f2;
  border-radius: 3px;
}
.feedback__list--checkbox input:checked + label::after {
  content: "";
  position: absolute;
  bottom: 7px;
  left: -44px;
  width: 32px;
  height: 32px;
  background: url("../img/tick-checked.svg") 100% 100% no-repeat;
}
.feedback__list--checkbox input:hover + label::before {
  background-color: #e5e5e5;
}
.feedback__list--checkbox input:active + label::before {
  background-color: #d5d5d5;
}
/* Cтили для радиокнопок */
.feedback__list--radio label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
}
.feedback__list--radio input + label::before {
  content: "";
  position: absolute;
  top: -15px;
  right: 206px;
  width: 52px;
  height: 52px;
  background-color: #f2f2f2;
  border-radius: 50%;
}
.feedback__list--radio input:checked + label::after {
  content: "";
  position: absolute;
  right: 222px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #000000;
}
